.TodoInsert{
  display: flex;
  background: #495057;
  input{
    background: none;
    outline: none;
    border: none;
    padding: 0.5rem;
    font-size: 1.125rem;
    line-height: 1.5;
    color: #fff;
    flex: 1;
    &::placeholder{color: #dee2e6;} /*자기 자신을 칭할때는 &기호*/
  }
  button{
    background: none;
    outline: none;
    border: none;
    background: #868e96;
    color: #fff;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: background 0.1s ease-in 0s;
    &:hover{
      background: #adb5bd;
    }
  }
}